<template>
  <div>
    <el-dialog :visible.sync="orderDialogVisible" title="填写订单">

      <el-form :model="bookInformation" :rules="rules" label-width="100px" style="width: 500px; margin-left: 75px" size="small " ref="flightForm">
        <h3>Flight Info</h3>
        <el-form-item label="行程" prop="fliNo">
          <el-input v-model="rowInfo.fliBaddress" style="width:168px" disabled />————
          <el-input v-model="rowInfo.fliAaddress" style="width:168px" disabled />
        </el-form-item>

        <el-form-item label="航空公司" prop="comCode">
          <el-input v-model="rowInfo.comCode" style="float: left;width: 400px" disabled />
        </el-form-item>

        <el-form-item label="起降时间" prop="fliBtime">
          <el-input v-model="rowInfo.fliBtime" style="width: 168px" disabled />————
          <el-input v-model="rowInfo.fliAtime" style="width: 168px" disabled />
        </el-form-item>
        <el-row>
          <el-col :span="8">
            <el-form-item label="经济舱价格" prop="fliYfare">
              <el-input v-model="rowInfo.fliYfare" style="float:left;width: 80px" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="公务舱价格" prop="fliYfare">
              <el-input v-model="rowInfo.fliCfare" style="float:left;width: 80px" disabled />
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="头等舱价格" prop="fliYfare">
              <el-input v-model="rowInfo.fliFfare" style="float:left;width: 80px" disabled />
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col :span="16">
            <h3>填写个人信息</h3>
          </el-col>
          <el-col :span="8">
            <el-button type="primary" icon="el-icon-plus" size="small" circle @click="add"></el-button>
          </el-col>
        </el-row>
        <div v-for="(pr,index) in bookInformation.persions" :key="pr.cusAccount">
          <el-form-item label="姓名" prop="cusAccount">
            <el-input v-model="pr.cusAccount" style="float: left;width: 400px" />
          </el-form-item>
          <el-form-item label="手机" prop="cusTelNumber">
            <el-input v-model="pr.cusTelNumber" style="float: left;width: 400px" />
          </el-form-item>
          <el-form-item label="身份证号码" prop="cusId">
            <el-input v-model="pr.cusId" style="float: left;width: 400px" />
          </el-form-item>
          <el-form-item label="舱位">
            <el-radio-group v-model="pr.booBerth" @change="changebooBerth($event,pr,index)">
              <el-radio label="头等舱">First Class</el-radio>
              <el-radio label="公务舱">Business Class</el-radio>
              <el-radio label="经济舱">Economic Class</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="票价">
            <el-input v-model="pr.fliYfare" style="float:left;width: 80px" disabled />
          </el-form-item>
          <hr>
        </div>
        <el-form-item>
          <el-button type="primary" @click="submit" :disabled="btnSts">Confirm</el-button>
          <el-button @click="orderDialogVisible=false">Cancel</el-button>
        </el-form-item>
      </el-form>

    </el-dialog>

    <el-dialog :visible.sync="paymentDialogVisible" title="订单信息">
      <el-form :model="rowInfo" label-width="100px" style="width: 500px; margin-left: 75px" size="small " ref="flightForm">
        <el-form-item label="行程">
          <el-input v-model="rowInfo.fliBaddress" style="width:168px" disabled />————
          <el-input v-model="rowInfo.fliAaddress" style="width:168px" disabled />
        </el-form-item>

        <el-form-item label="起降时间">
          <el-input v-model="rowInfo.fliBtime" style="width: 168px" disabled />————
          <el-input v-model="rowInfo.fliAtime" style="width: 168px" disabled />
        </el-form-item>

        <el-form-item label="航空公司" prop="comCode">
          <el-input v-model="rowInfo.comCode" style="float: left;width: 400px" disabled />
        </el-form-item>

        <h3>Information</h3>
        <div v-for="(pr,index) in bookInformation.persions" :key="index">
          <el-form-item label="姓名">
            <el-input v-model="pr.cusAccount" style="float: left;width: 400px" disabled />
          </el-form-item>
          <el-form-item label="手机">
            <el-input v-model="pr.cusTelNumber" style="float: left;width: 400px" disabled />
          </el-form-item>
          <el-form-item label="身份证号码">
            <el-input v-model="pr.cusId" style="float: left;width: 400px" disabled />
          </el-form-item>
          <el-form-item label="舱位">
            <el-input v-model="pr.booBerth" style="float:left;width: 80px" disabled />
          </el-form-item>
          <el-form-item label="票价">
            <el-input v-model="pr.fliYfare" style="float:left;width: 80px" disabled />
          </el-form-item>
          <hr>
        </div>
        <el-form-item>
          <el-button @click="prestep">Return</el-button>
          <el-button type="primary" @click="registry" :disabled="btnSts">Submit</el-button>
          <el-button type="primary" @click="pay" :disabled="btnSts">Pay Now</el-button>
          <el-button @click="cancel">Cancel</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <div style="text-align: center ;margin-bottom: 30px ">
      Departure：
      <el-input v-model="flight.fliBaddress" placeholder="Departure" style="margin-top: 50px"></el-input>
      Arrival：
      <el-input v-model="flight.fliAaddress" placeholder="Arrival" style="margin-top: 50px"></el-input>
      Arrival Time：
      <el-date-picker value-format="yyyy-MM-dd HH:mm:ss" v-model="flight.fliBtime" type="datetime" placeholder="Select Date" default-time="12:00:00">
      </el-date-picker>

      <el-button type="primary" icon="el-icon-search" style="margin: 20px" @click="findList1">Search</el-button>
    </div>

    <el-table :data="flightInformationList" width="100%">
      <el-table-column prop="comCode" label="Info" ></el-table-column>
      <el-table-column prop="fliBaddress" label="Departure"></el-table-column>
      <el-table-column prop="fliAaddress" label="Arrival"></el-table-column>
      <el-table-column prop="fliBtime" label="Departure Time"></el-table-column>
      <el-table-column prop="fliAtime" label="Arrival Time"></el-table-column>
      <el-table-column prop="fliYfare" label="Economic Price" align="center"></el-table-column>
      <el-table-column prop="fliCfare" label="Business Price" align="center"></el-table-column>
      <el-table-column prop="fliFfare" label="First-Class" align="center"></el-table-column>
      <el-table-column label="Opeartion">
        <template slot-scope="scope">
          <el-button size="mini" @click="booking(scope.$index,scope.row)">Book</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination style="margin-top: 10px" background layout="prev, pager, next" :current-page.sync="pageInfo.pageNum" :page-size="pageInfo.pageSize" :total="pageInfo.total">
    </el-pagination>
    <el-dialog title="立即支付" :visible.sync="dialogVisible1" width="30%" :before-close="handleClose">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="支付金额">
          <el-input v-model="paynum" disabled></el-input>
        </el-form-item>
        <el-form-item label="支付密码">
          <el-input v-model="payPwd"></el-input>
        </el-form-item>
        <el-form-item label="支付方式">
          <el-select v-model="payType" placeholder="Please Select">
            <el-option v-for="item in payoptions" :key="item.username" :label="item.name" :value="item.username">
            </el-option>
          </el-select>

        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible1 = false">Cancel</el-button>
            <el-button type="primary" @click="onpay">Confirm</el-button>
        </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
    name: "FlightInformationList",
    created() {
        debugger
        this.flight.fliBaddress = this.$route.query.fromP
        this.flight.fliAaddress = this.$route.query.toP
        this.findList();
        this.cusAccount = this.$route.query.cusAccount;
        axios.get("/api/airLink/list", {}).then(resp => {
            this.companyOptions = resp.data.data

        })
    },
    methods: {

        formateA(row, column) {
            var realValue = ''
            this.companyOptions.forEach(function (item, index) {
                if (row.comCode == item.username) {
                    realValue = item.name
                    return
                }
            })
            return realValue;
        },
        handleClose() {
            this.dialogVisible1 = false
        },
        onpay() {
            let user = JSON.parse(window.localStorage.getItem("user"))
            if (this.payPwd == '') {
                this.$message({
                    type: 'error',
                    message: 'Please input password'
                })
                return
            } else {
                if (this.payPwd == user.payPwd) {
                    axios.post("/api/payerLink/pay", {
                        name: user.username,
                        payNum: this.paynum,
                        reserve2: this.payType
                    }).then(resp => {
                        if (resp.data.success) {
                            this.$message({
                                type: 'success',
                                message: 'Paied'
                            })
                            this.bookInformation.flagPay = 1;
                            this.bookInformation.reserve3 = this.payType
                            this.saveOrder()

                            this.dialogVisible1 = false
                        } else {
                            this.$message({
                                type: 'erro',
                                message: resp.data.message
                            })
                        }
                    })
                } else {
                    this.$message({
                        type: 'error',
                        message: 'Payment password error'
                    })
                }
            }

        },
        pay() {
            let paynum = 0
            this.bookInformation.persions.forEach(item => {
                paynum = paynum + parseInt(item.fliYfare)

            })
            this.dialogVisible1 = true

            this.paynum = paynum
            this.bookInformation.reserve2 = this.paynum
            axios.get("/api/payerLink/list", {}).then(res => {
                this.payoptions = res.data.data
            })

        },
        changebooBerth(value, item, index) {

            if (value == "First class") {
                item.fliYfare = this.rowInfo.fliFfare

            } else if (value == "Economy class") {
                item.fliYfare = this.rowInfo.fliYfare

            } else if (value == "Business class") {
                item.fliYfare = this.rowInfo.fliCfare

            }
            this.$set(this.bookInformation.persions, index, item)
        },
        add() {
            this.bookInformation.persions.push({
                cusAccount: "",
                comCode: "",
                cusId: "",
            })
        },
        /**
         * 获取表格内数据条件查询
         */
        findList() {
            axios.post("/api/airLink/airList", {
                pageNum: this.pageInfo.pageNum,
                pageSize: this.pageInfo.pageSize,
                search: '',
                fliBaddress: this.flight.fliBaddress,
                fliAaddress: this.flight.fliAaddress,
                fliBtime: this.flight.fliBtime,

            }).then(resp => {
                //console.log(resp.data);
                // this.pageInfo.pageSize = this.pageInfo.pageSize;
                // this.pageInfo.pageNum = this.pageInfo.pageNum;
                // this.pageInfo.total = resp.data.total;
                // this.pageInfo.pages = resp.data.data;
                //将数据赋值给managerList表格数据
                this.flightInformationList = resp.data
            })
        },
        findList1() {
            axios.post("/api/airLink/airList", {
                pageNum: this.pageInfo.pageNum,
                pageSize: this.pageInfo.pageSize,
                fliBaddress: this.flight.fliBaddress,
                fliAaddress: this.flight.fliAaddress,
                fliBtime: this.flight.fliBtime,
                search: '',

            }).then(resp => {
                //console.log(resp.data);
                // this.pageInfo.pageSize = this.pageInfo.pageSize;
                // this.pageInfo.pageNum = this.pageInfo.pageNum;
                // this.pageInfo.total = resp.data.total;
                // this.pageInfo.pages = resp.data.data;
                // //将数据赋值给managerList表格数据
                this.flightInformationList = resp.data;
            })
        },
        payment() {
            this.$confirm('Confirm and pay. Do you want to continue?', 'Notice', {
                confirmButtonText: 'Confirm',
                cancelButtonText: 'Cancel',
                type: 'warning'
            }).then(() => {
                //发送请求去删除对应的数据
                axios.delete('/bookInformation/payment/' + this.rowInfo.fliNo).then(resp => {
                    if (resp.data === 'success') {
                        this.$message({
                            type: 'success',
                            message: 'Paied'
                        })
                    } else {
                        this.$message({
                            type: 'error',
                            message: 'Payment failure!'
                        })
                    }
                    //重新获取表格数据
                    this.findList();
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: 'Payment canceled'
                });
            });

        },
        cancel() {
            this.bookInformation.flagPay = 0;
            axios.post("/bookInformation/save", this.bookInformation).then(response => {
                if (response.data === 'success') {
                    this.$message({
                        type: 'info',
                        message: 'Payment cancelled, order saved'
                    });
                } else {
                    this.$message({
                        type: 'error',
                        message: 'Order not saved!'
                    })
                }
            })
            paymentDialogVisible = false;
        },
        booking(index, row) {
            this.airname = row.reserve1;
            if (this.cusAccount != '') {
                this.rowInfo = row;

                this.orderDialogVisible = true;
            } else {
                this.$message({
                    title: 'notice',
                    message: "unlogged in",
                    type: 'warning'
                })

            }
        },
        update() {
            //将提交按钮设置为不可用状态 按钮动态加disable属性
            this.btnSts = true;
            //1、先验证表单格式没问题了，再允许发送请求
            this.$refs['flightForm'].validate((valid) => {
                if (valid) {
                    let payLoad = this.flight;
                    // 进行表单的提交
                    axios.post("/air-api/flight/save", payLoad).then(response => {
                        this.btnSts = false;
                        if (response.data === 'success') {
                            this.$notify({
                                title: "notice",
                                message: "User saved successfully",
                                type: "success"
                            });
                            //一旦提交成功，自动关闭窗口
                            this.orderDialogVisible = false;
                            //刷新表格数据
                            this.findList();
                        }
                    })
                } else {
                    this.btnSts = false;
                    return false;
                }
            })
        },
        /**
         * 退票
         */
        refundticket(index, row) {
            this.$confirm('This operation will permanently delete the order. Do you want to continue?', 'notice', {
                confirmButtonText: 'Confirm',
                cancelButtonText: 'Cancel',
                type: 'warning'
            }).then(() => {
                axios.delete("/bookInformation/delete/" + row.id).then(resp => {
                    if (resp.data === "success") {
                        this.$message({
                            type: 'success',
                            message: 'Deleted successfully!'
                        })
                        this.findList();

                    } else {
                        this.$message({
                            type: 'error',
                            message: 'this.findList();Deletion failed!'
                        })
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: 'Undeleted'
                });
            });
            this.ticketDialogVisible = false;
        },
        /**
         * 查询票
         */
        ticket() {

            axios.post("/bookInformation/Ticketpage", {
                pageNum: this.pageInfo.pageNum,
                pageSize: this.pageInfo.pageSize,
                condition: this.customer,

            }).then(resp => {
                console.log(resp.data);
                this.pageInfo.pageSize = resp.data.pageSize;
                this.pageInfo.pageNum = resp.data.pageNum;
                this.pageInfo.total = resp.data.total;
                this.pageInfo.pages = resp.data.pages;
                //将数据赋值给managerList表格数据
                this.bookInformationList = resp.data.list;
                this.ticketDialogVisible = true;
            })
        },
        submit() {
            var date1 = new Date();
            var time = date1.getFullYear() + "" + date1.getMonth() + "" + date1.getDate() + "" + date1.getDay() + "" + date1.getHours() + "" + date1.getMinutes() + "" + date1.getSeconds()
            //将提交按钮设置为不可用状态
            this.btnSts = true;
            //1.先验证表单没问题了在发送
            let form = this.$refs['flightForm'];
            this.bookInformation.comCode = this.rowInfo.comCode;
            this.bookInformation.booNo = this.rowInfo.fliNo;
            this.bookInformation.boobAddress = this.rowInfo.fliBaddress;
            this.bookInformation.booAAddress = this.rowInfo.fliAaddress;
            this.bookInformation.booAtime = this.rowInfo.fliAtime + '';
            this.bookInformation.boobTime = this.rowInfo.fliBtime + '';
            this.bookInformation.booBerth = 'Economy class';
            this.bookInformation.booOrderNum = this.customer.cusAccount + '' + time,
                this.bookInformation.booFare = this.rowInfo.fliYfare;
            this.bookInformation.flag = 0;
            form.validate((valid) => {
                if (valid) {
                    this.btnSts = false;
                    this.orderDialogVisible = false;
                    this.paymentDialogVisible = true;

                } else {
                    this.btnSts = false;
                    return false;
                }
            })
        },
        saveOrder() {

            let user = JSON.parse(window.localStorage.getItem("user"))
            if (user == null) {
                this.$message({
                    showClose: true,
                    message: 'Please log in',
                    type: 'error'
                });
            }
            this.bookInformation.reserve4 = this.airname
            this.bookInformation.reserve1 = user.username;
            axios.post("/api/book/save", this.bookInformation).then(response => {

                if (response.data.success) {
                    this.$message({
                        title: 'notice',
                        message: "Submitted successfully",
                        type: 'success'
                    })
                } else {
                    this.$message({
                        type: 'error',
                        message: 'Submitted failed'
                    })
                }
                this.clear();
                this.paymentDialogVisible = false;
            })
        },
        registry() {
            let paynum = 0
            this.bookInformation.persions.forEach(item => {
                paynum = paynum + parseInt(item.fliYfare)

            })

            this.paynum = paynum
            this.bookInformation.reserve2 = this.paynum

            this.bookInformation.flagPay = 0;
            this.$confirm('Confirm and submit. Do you want to continue?', 'notice', {
                confirmButtonText: 'Confirm',
                cancelButtonText: 'Cancel',
                type: 'warning'
            }).then(() => {
                this.saveOrder()
            });

        },
        /**
         * 上一步
         */
        prestep() {
            this.orderDialogVisible = true;
            this.paymentDialogVisible = false;
        },

        clear() {
            this.bookInformation = {
                cusAccount: "",
                comCode: "",
                cusId: "",
                cusTelNumber: "",
                booNo: "",
                boobAddress: "",
                booAAddress: "",
                boobTime: "",
                booAtime: "",
                booBerth: "",
                booNumber: "",
                booFare: "",
                booTime: "",
                flagPay: 0,
                flag: 0,
            };

        },

    },
    data() {
        let validPhone = (rules, value, callback) => {
            if (!value || value === '') {
                return callback(new Error("The phone number cannot be empty!"))
            }
            let pattern = /^1[3|4|5|8][0-9]\d{8}$/;
            if (!pattern.test(value)) {
                return callback(new Error("Mobile phone number format error! Please re-enter"));
            }
            return callback();
        };

        //通过正则表达式验证身份证
        let validId = (rules, value, callback) => {
            if (!value || value === '') {
                return callback(new Error("The ID card cannot be empty"))
            }
            let pattern = /^[1-9][0-9]{5}(19|20)[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|31)|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}([0-9]|x|X)$/;
            if (!pattern.test(value)) {
                return callback(new Error("Id card format error! Please re-enter!"))
            }
            return callback();
        };

        let validAccount = (rule, value, callback) => {
            //判断姓名长度大于2，不能为空
            if (!value || value === '') {
                return callback(new Error("The name cannot be empty!"));
            }
            if (value.length < 2) {
                return callback(new Error("The length of the name cannot be less than 2"));
            }
            return callback();
        };

        return {
            //pageInfo对象实际是从后台查询得到的，代表了分页必要的信息
            pageInfo: {
                pageNum: 1,
                /*当前是那一页*/
                pageSize: 100,
                /*每页展示多少条*/
                pages: 1,
                /*总共有多少页*/
                total: 100 /*总共有多少条数据*/
            },
            dialogVisible1: false,
            companyOptions: [],
            payoptions: [],
            payPwd: '',
            payType: '',
            paynum: 0,
            airname: "",
            drawer: false,
            ticketDialogVisible: false,
            orderDialogVisible: false,
            paymentDialogVisible: false,
            refundVisible: false,
            loginVisible: true,
            onlineVisible: false,
            bookInformation: {
                persions: [],

                cusTelNumber: "",
                booNo: "",
                boobAddress: "",
                booAAddress: "",
                boobTime: "",
                booAtime: "",
                booBerth: "",
                booNumber: "",
                booOrderNum: "",
                booFare: "",
                booTime: "",
                flagPay: 0,
                flag: 0,
            },
            flight: {
                fliNo: "", //航班编号
                airCode: "", //飞机编号
                comCode: "", //公司编号
                fliBaddress: '', //起始地址
                fliAaddress: '', //到达地址
                fliBtime: '', //起飞时间
                fliAtime: '', //到达时间
                fliFfare: '', //头等舱价格
                fliCfare: '', //公务舱价格
                fliYfare: '', //经济舱价格
                fliFnumber: '', //头等舱剩余人数
                fliCnumber: '', //公务舱剩余人数
                fliYnumber: '', //经济舱剩余人数
                flag: 0,
                fliDiscount: 0, //折扣
                fliRefundtime: 0, //退票记录时间
            },
            customer: {
                cusAccount: "",
                cusPwd: "",
                cusId: "",
                cusSex: "",
                cusTelNumber: "",
                cusEmail: "",
                cusTime: "",
                cusIntegral: "",
                farId: "",
                cusName: "",
                cusType: "",
                flag: ""
            },
            bookInformationList: [],
            rowInfo: {
                fliNo: "", //航班编号
                airCode: "", //飞机编号
                comCode: "", //公司编号
                fliBaddress: '', //起始地址
                fliAaddress: '', //到达地址
                fliBtime: '', //起飞时间
                fliAtime: '', //到达时间
                fliFfare: '', //头等舱价格
                fliCfare: '', //公务舱价格
                fliYfare: '', //经济舱价格
                fliFnumber: '', //头等舱剩余人数
